          <path class="path4"
                d="M21.677 9.806c-0.569 0-1.032 0.463-1.032 1.032v14.452c0 0.569 0.463 1.032 1.032 1.032s1.032-0.463 1.032-1.032v-14.452c0-0.569-0.463-1.032-1.032-1.032z"></path>
        </symbol>
        <symbol id="icon-clock" viewBox="0 0 32 32">
          <title>clock</title>
          <path class="path1" fill="#605f5f"
                d="M16 29c-7.168 0-13-5.831-13-13s5.832-13 13-13c7.168 0 13 5.832 13 13s-5.832 13-13 13zM16 0c-8.822 0-16 7.178-16 16s7.178 16 16 16c8.822 0 16-7.178 16-16s-7.178-16-16-16z"></path>
          <path class="path2" fill="#605f5f"
                d="M23.958 21.837l-6.958-6.489v-6.282c0-0.827-0.673-1.5-1.5-1.5s-1.5 0.673-1.5 1.5v6.934c0 0.414 0.174 0.814 0.477 1.098l7.435 6.934c0.279 0.259 0.642 0.402 1.023 0.402 0.415 0 0.814-0.174 1.096-0.477 0.564-0.605 0.532-1.555-0.073-2.12z"></path>
        </symbol>
      </defs>
    </svg>
    <div class="container">
      <div class="cart">
        <div class="page-title-normal">
          <h2 class="page-title-h2"><span>My Cart</span></h2>
        </div>
        <div class="item-list-wrap">
          <div class="cart-item">
            <div class="cart-item-head">
              <ul>
                <li>Items</li>
                <li>Price</li>
                <li>Quantity</li>
                <li>Subtotal</li>
                <li>Edit</li>
              </ul>
            </div>
            <ul class="cart-item-list">
              <li v-for="item in cartList">
                <div class="cart-tab-1">
                  <div class="cart-item-check">
                    <a href="javascipt:void(0);" class="checkbox-btn item-check-btn" :class="{'checked': item.checked === '1'}" @click="editCartItem('checked', item)">
                      <svg class="icon icon-ok">
                        <use xlink:href="#icon-ok"></use>
                      </svg>
                    </a>
                  </div>
                  <div class="cart-item-pic">
                    <img :src="'../../static/' + item.productImage">
                  </div>
                  <div class="cart-item-title">
                    <div class="item-name">{{ item.productName }}</div>
                  </div>
                </div>
                <div class="cart-tab-2">
                  <div class="item-price">{{ item.salePrice | currency('¥')}}</div>
                </div>
                <div class="cart-tab-3">
                  <div class="item-quantity">
                    <div class="select-self select-self-open">
                      <div class="select-self-area">
                        <a class="input-sub" @click="editCartItem('reduce', item)">-</a>
                        <span class="select-ipt">{{ item.productNum }}</span>
                        <a class="input-add" @click="editCartItem('add', item)">+</a>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="cart-tab-4">
                  <div class="item-price-total">{{ item.productNum * item.salePrice | currency('¥')}}</div>
                </div>
                <div class="cart-tab-5">
                  <div class="cart-item-opration">
                    <a href="javascript:void(0);" class="item-edit-btn" @click="deleteFromCartConfirm(item)">
                      <svg class="icon icon-del">
                        <use xlink:href="#icon-del"></use>
                      </svg>
                    </a>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
        <div class="cart-foot-wrap">
          <div class="cart-foot-inner">
            <div class="cart-foot-l">
              <div class="item-all-check" @click="selectAll">
                <a href="javascipt:void(0);">
                  <span class="checkbox-btn item-check-btn" :class="{'checked': selectAllFlag}">
                      <svg class="icon icon-ok"><use xlink:href="#icon-ok"/></svg>
                  </span>
                  <span>Select all</span>
                </a>
              </div>
            </div>
            <div class="cart-foot-r">
              <div class="item-total">
                Item total: <span class="total-price">{{ totalPrice | currency('¥') }}</span>
              </div>
              <div class="btn-wrap">
                <a class="btn btn--red" :class="{'btn--dis': checkedCount===0}"  @click="checkOut">Checkout</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <module :mdShow="mdShowConfirmFlag" @closeModule="closeModule">
      <h3 slot="message">
        确定要删除该商品吗
      </h3>
      <div slot="btnGroup">
        <a href="javascript:void(0)" class="btn btn--m" @click="closeModule">返回</a>
        <a href="javascript:void(0)" class="btn btn--m" @click="deleteFromCart">确定</a>
      </div>
    </module>
    <nav-footer></nav-footer>
  </div>
</template>

<script>
  import axios from 'axios'

  import '../assets/css/checkout.css'

  import NavHeader from "./../components/NavHeader";
  import NavBreadCrumb from "./../components/NavBreadCrumb"
  import NavFooter from "./../components/NavFooter";
  import Module from "./../components/Module"

  export default {
    data(){
      return {
        cartList: [],
        mdShowConfirmFlag: false,
        productId: '',
        deleteItem: null,
      }
    },

    components:{
      NavHeader,
      NavBreadCrumb,
      NavFooter,
      Module
    },

    methods: {
      getCartList(){
        axios.get('/users/getCartList', {})
          .then((response)=>{
            let res = response.data;
            if(res.status === '0'){
              this.cartList = res.result.cartList
            }else{
              console.log("未登录")
            }
          })
      },

      deleteFromCartConfirm(item){
        this.mdShowConfirmFlag = true;
        this.productId = item.productId;
        this.deleteItem = item;
      },

      deleteFromCart(){
        axios.post('/users/deleteFromCart', {productId: this.productId})
          .then((response)=>{
            let res = response.data;
            if(res.status === '0'){
              // 删除成功，刷新页面
              this.mdShowConfirmFlag = false;
              let updateNum = this.deleteItem.checked === '1' ? -parseInt(this.deleteItem.productNum) : 0;
              this.$store.commit('updateCartCount', updateNum);
              this.getCartList();
            }else{
              console.log("删除购物车商品失败")
            }
          })
      },

      editCartItem(flag, item){
        let updateNum = 0;
        switch (flag) {
          case 'add':
            item.productNum++;
            updateNum = item.checked==='1'? 1 : 0;
            break;
          case 'reduce':
            if(item.productNum>1){
              item.productNum--;
              updateNum = item.checked==='1'? -1 : 0;
            }
            break;
          case 'checked':
            item.checked = item.checked==='1'?'0':'1';
            updateNum = item.checked === '1' ? parseInt(item.productNum) : -parseInt(item.productNum);
            break;
        }
        this.$store.commit('updateCartCount', updateNum);
        let params = {
            productId: item.productId,
            productNum: item.productNum,
            checked: item.checked
        };
        axios.post('/users/editCartItem', params)
          .then((response)=>{
            let res = response.data
          });
      },

      closeModule(){
        this.mdShowConfirmFlag = false;
      },

      selectAll(){
        // 由于selectAllFlag属性为计算属性，无法更改，重新定义变量flag来操作
        let flag = !this.selectAllFlag;
        let totalNum = 0;
        this.cartList.forEach((item)=>{
           item.checked = flag ? '1' : '0';
           totalNum += parseInt(item.productNum);
        });
        let updateNum = flag ? totalNum : 0;
        this.$store.commit('getCartCount', updateNum);
        axios.post('/users/selectAll', {selectAllFlag: flag})
          .then((response)=>{
            let res = response.data
          })
      },

      checkOut(){
        if(this.checkedCount > 0){
          this.$router.push({
              path: '/address'
          })
        }
      }
    },

    computed: {
      selectAllFlag(){
        return this.cartList.length === this.checkedCount;
      },

      checkedCount(){
        let i = 0;
        this.cartList.forEach((item)=>{
          if(item.checked === '1'){
            i++;
          }
        });
        return i;
      },

      totalPrice(){
        let totalPrice = 0;
        this.cartList.forEach((item)=>{
          if(item.checked === '1'){
            totalPrice += parseInt(item.productNum) * parseFloat(item.salePrice)
          }
        });
        return totalPrice;
      },
    },

    mounted() {
      this.getCartList();
    }
  }
</script>

<style scoped>

</style>



