<template>
  <div>
    <div class="md-modal modal-msg md-modal-transition" :class="{'md-show': mdShow}">
      <div class="md-modal-inner">
        <div class="md-top">
          <button class="md-close" @click="closeIt">Close</button>
        </div>
        <div class="md-content">
          <div class="confirm-tips">
            <slot name="message"></slot>
          </div>
          <div class="btn-wrap">
            <slot name="btnGroup"></slot>
          </div>
        </div>
      </div>
    </div>
    <div class="md-overlay" v-if="mdShow" @click="closeIt"></div>
  </div>
</template>

<script>
    export default {
      props: [
        "mdShow"
      ],

      data(){
        return {

        }
      },

      methods:{
        closeIt(){
          // 通过该函数向父组件传递trigger指令，触发父组件closeModule函数
          this.$emit("closeModule")
        }
      }
    }
</script>

<style scoped>

</style>



