      </svg>
      <div class="container">
        <div class="checkout-addr">
          <div class="page-title-normal">
            <h2 class="page-title-h2"><span>check out</span></h2>
          </div>
          <!-- process step -->
          <div class="check-step">
            <ul>
              <li class="cur"><span>Confirm</span> address</li>
              <li><span>View your</span> order</li>
              <li><span>Make</span> payment</li>
              <li><span>Order</span> confirmation</li>
            </ul>
          </div>

          <!-- address list -->
          <div class="page-title-normal checkout-title">
            <h2><span>Shipping address</span></h2>
          </div>
          <div class="addr-list-wrap">
            <div class="addr-list">
              <ul>
                <li v-for="(item, index) in addressListFilter" @click="checkThis=index; selectedAddressId=item.addressId" :class="{'check': checkThis===index}">
                  <dl>
                    <dt>{{ item.userName }}</dt>
                    <dd class="address">{{ item.streetName }}</dd>
                    <dd class="tel">{{ item.tel }}</dd>
                  </dl>
                  <div class="addr-opration addr-del">
                    <a href="javascript:;" class="addr-del-btn" @click="deleteAddressConfirm(item.addressId)">
                      <svg class="icon icon-del"><use xlink:href="#icon-del"></use></svg>
                    </a>
                  </div>
                  <div class="addr-opration addr-set-default" v-if="!item.isDefault">
                    <a href="javascript:;" class="addr-set-default-btn" @click="setDefaultAddress(item.addressId)"><i>Set default</i></a>
                  </div>
                  <div class="addr-opration addr-default" v-if="item.isDefault">Default address</div>
                </li>
                <li class="addr-new">
                  <div class="add-new-inner">
                    <i class="icon-add">
                      <svg class="icon icon-add"><use xlink:href="#icon-add"></use></svg>
                    </i>
                    <p>Add new address</p>
                  </div>
                </li>
              </ul>
            </div>

            <div class="shipping-addr-more">
              <a class="addr-more-btn up-down-btn" href="javascript:void(0);" :class="{'open': displayAllFlag}" @click="displayAll">
                more
                <i class="i-up-down">
                  <i class="i-up-down-l"></i>
                  <i class="i-up-down-r"></i>
                </i>
              </a>
            </div>
          </div>

          <!-- shipping method-->
          <div class="page-title-normal checkout-title">
            <h2><span>Shipping method</span></h2>
          </div>
          <div class="lemall-msg-info hidden">
            <span>The region you selected is not within our delivery area. Please select another shipping address within our delivery areas.</span>
          </div>
          <div class="shipping-method-wrap">
            <div class="shipping-method">
              <ul>
                <li class="check">
                  <div class="name">Standard shipping</div>
                  <div class="price">Free</div>
                  <div class="shipping-tips">
                    <p>Once shipped，Order should arrive in the destination in 1-7 business days</p>
                  </div>
                </li>
              </ul>
            </div>
          </div>
          <div class="next-btn-wrap">
            <router-link class="btn btn--m btn--red" :to="{path: '/orderConfirm', query: {'addressId': selectedAddressId}}">Next</router-link>
          </div>
        </div>
      </div>
    </div>
    <module :mdShow="mdDeleteAddressConfirmFlag" @closeModule="closeModule">
      <h3 slot="message">
        确认删除该收获地址？
      </h3>
      <div slot="btnGroup">
        <a href="javascript:void(0)" class="btn btn--m" @click="closeModule">返回</a>
        <a href="javascript:void(0)" class="btn btn--m" @click="deleteAddress">确定</a>
      </div>
    </module>
    <nav-footer></nav-footer>
  </div>
</template>

<script>
  import axios from 'axios';

  import NavHeader from "./../components/NavHeader";
  import NavBreadCrumb from "./../components/NavBreadCrumb"
  import NavFooter from "./../components/NavFooter";
  import Module from "./../components/Module"

  export default {
    data(){
      return {
        limitDisplay: 3,
        displayAllFlag: false,
        checkThis: null,
        addressList: [],
        mdDeleteAddressConfirmFlag: false,
        selectedAddressId: null,
        addressId: null,
      }
    },

    computed: {
      addressListFilter(){
        return this.displayAllFlag ? this.addressList : this.addressList.slice(0, this.limitDisplay)
      }
    },

    components: {
      NavHeader,
      NavBreadCrumb,
      NavFooter,
      Module
    },

    mounted: function(){
      this.getAddressList();
    },

    methods: {
      getAddressList(){
        axios.get('/users/getAddressList', {})
          .then((response)=>{
            let res = response.data;
            if(res.status === '0'){
              this.addressList = res.result.addressList;
              this.addressList.forEach((item, index)=>{
                if(item.isDefault){
                  this.checkThis = index;
                  this.selectedAddressId = item.addressId;
                }
              })
            }else{
              console.log('未登录')
            }
          })
      },

      displayAll(){
        this.displayAllFlag = !this.displayAllFlag
      },

      setDefaultAddress(addressId){
        axios.post('/users/setDefaultAddress', {"addressId": addressId})
          .then((response)=>{
            let res = response.data;
            if(res.status === '0'){
              console.log("修改默认地址成功");
              this.getAddressList();
            }
          })
      },

      closeModule(){
        this.mdDeleteAddressConfirmFlag = false;
      },

      deleteAddressConfirm(addressId){
        this.mdDeleteAddressConfirmFlag = true;
        this.addressId = addressId;
      },

      deleteAddress(){
        axios.post('/users/deleteAddress', {"addressId": this.addressId})
          .then((response)=>{
            let res = response.data;
            if(res.status === '0'){
              console.log('删除收获地址成功');
              this.mdDeleteAddressConfirmFlag = false;
              this.getAddressList();
            }
          })
      },


    },

  }
</script>

<style scoped>

</style>



